body {
    font-size: $default-font-size;
    line-height: $default-font-size;
}

header {
    .menu {
        line-height: 1.25rem;
        font-size: 1.25rem;
        gap: $default-font-size * 5;
    }
}

footer {
    padding: ($default-font-size * 3) 0;

    .menu {
        gap: $default-font-size * 3;
    }

    .social-icons img {
        width: 48px;
    }
}

header,
footer {
    background-color: $color-primary-main;
}

.logo {
    width: 80px;
    margin: 16px 0;
}

.menu {
    display: flex;

    a {
        text-decoration: none;
        color: #fff;
    }
}

.contact-ways {
    padding: 36px 0 32px 0;
    margin-bottom: $default-font-size * 2;
    color: #fff;
    font-size: 36px;
    line-height: 54px;
    border-bottom: 1px solid #fff;

    li {
        margin-bottom: 8px;
    }

    i {
        margin-right: 20px;
    }
}

.tnc {
    color: #fff;
}
